import React from 'react'
// import { makeStyles } from '@material-ui/core/styles'
// import Paper from '@material-ui/core/Paper'
import Box from '@material-ui/core/Box'
import Grid from '@material-ui/core/Grid'
import TextField from '@material-ui/core/TextField'
import { connect } from 'react-redux'
import {
    TALE_UPDATE_INFO
} from '../../../redux/actionTypes'

// const useStyles = makeStyles(theme => ({
//     root: {
//       flexGrow: 1,
//     },
//     paper: {
//       padding: theme.spacing(2),
//       textAlign: 'center',
//       color: theme.palette.text.secondary,
//     },
// }))

const updateTaleInfo = (dispatch, key, val, oldInfo) => {
    dispatch({
        type: TALE_UPDATE_INFO,
        payload: {
            ...oldInfo,
            [key]: val
        }
    })
}

const CreateTaleIntro = props => {
    // const classes = useStyles()
    const {info, typingVal} = props

    return (
        <Box py={3}>
            <Grid container spacing={2}>
                <Grid item xs={12} sm={6} md={3}>
                    <TextField 
                        fullWidth 
                        label="Name of Tale" 
                        variant="outlined"
                        value={info.name} 
                        onChange={e => typingVal('name', e.target.value, info)}
                    />
                </Grid>
                <Grid item xs={12} sm={6} md={3}>
                    <TextField 
                        fullWidth 
                        label="Category" 
                        variant="outlined" 
                        value={info.category} 
                        onChange={e => typingVal('category', e.target.value, info)}
                    />
                </Grid>
                <Grid item xs={12} sm={6} md={3}>
                    <TextField 
                        fullWidth 
                        label="Tags" 
                        variant="outlined"
                        value={info.tags} 
                        onChange={e => typingVal('tags', e.target.value, info)}
                    />
                </Grid>
                <Grid item xs={12} sm={6} md={3}>
                    <TextField 
                        fullWidth 
                        label="Language" 
                        variant="outlined" 
                        value={info.lang} 
                        onChange={e => typingVal('lang', e.target.value, info)}
                    />
                </Grid>
                <Grid item xs={12}>
                    <TextField
                        label="Brief description"
                        multiline
                        rows="4"
                        fullWidth
                        variant="outlined"
                        value={info.desc} 
                        onChange={e => typingVal('desc', e.target.value, info)}
                    />
                </Grid>
            </Grid>
        </Box>
    )
}

const mapStateToProps = state => ({
    info: state.wipTale.info
})
const mapDispatchToProps = dispatch => ({
    typingVal: (key, val, oldInfo) => updateTaleInfo(dispatch, key, val, oldInfo)
})

export default connect(mapStateToProps, mapDispatchToProps)(CreateTaleIntro)