import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';

const useStyles = makeStyles(theme => ({
  modalWrapper: {
    overflow: "auto"
  },
  modalBox: {
    position: 'relative',
    width: `${window.innerWidth - 200}px`,
    minWidth: 300,
    backgroundColor: theme.palette.background.paper,
    border: `5px double ${theme.palette.primary.main}`,
    boxShadow: theme.shadows[5]
  },
  titleBar: {
    margin: 0,
    backgroundColor: theme.palette.primary.main,
    color: '#fff',
    padding: `${theme.spacing(.5)}px ${theme.spacing(1)}px`
  },
  modalBody: {
    padding: `${theme.spacing(2)}px ${theme.spacing(1)}px`
  }
}));

const getDynamicStyle = () => ({
  width: `${window.innerWidth - 200}px`,
  margin: `${window.innerHeight / 5}px auto`
})

const SimpleModal = props => {
  const classes = useStyles();
  const { isOpen, handleClose, title, children } = props
  
  return (
      <Modal
        aria-labelledby={title}
        aria-describedby={title}
        className={classes.modalWrapper}
        open={isOpen}
        onClose={handleClose}
      >
        <div className={classes.modalBox} style={getDynamicStyle()}>
          <h2 className={classes.titleBar}>{title}</h2>
          <div className={classes.modalBody}>
            {children}
          </div>
        </div>
      </Modal>
  );
}

export default SimpleModal
