import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Button from '@material-ui/core/Button'
import IconButton from '@material-ui/core/IconButton'
import MenuIcon from '@material-ui/icons/Menu'
import logoImg from '../../../assets/img/logo/Reactale-logo-50h-white-bg.png'
import styles from './style.module.css'
import { connect } from 'react-redux'
import { UI_SIDEBAR_OPEN } from '../../../redux/actionTypes'

const useStyles = makeStyles(theme => ({
  menuButton: {
    marginRight: theme.spacing(2),
  }
}))

const Header = props => {
  const classes = useStyles()
  // console.log(styles)
  return (
    <div className="flex-grow-1">
      <AppBar position="static">
        <Toolbar>
          <IconButton edge="start" onClick={props.openSidebar} className={classes.menuButton} color="inherit" aria-label="menu">
            <MenuIcon />
          </IconButton>
          <div className={styles.logoContainer}>
            <img src={logoImg} alt="Reactale Logo" className={styles.logoImg} />
          </div>          
          <Button color="inherit">Login</Button>
        </Toolbar>
      </AppBar>
    </div>
  )
}

const mapDispatchToProps = dispatch => ({
  openSidebar: () => dispatch({type: UI_SIDEBAR_OPEN})
})

export default connect(null, mapDispatchToProps)(Header)
