import React from 'react'
import { Link } from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles'
import Drawer from '@material-ui/core/Drawer'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import { connect } from 'react-redux'
import {UI_SIDEBAR_CLOSE} from '../../../redux/actionTypes'
import HomeIcon from '@material-ui/icons/Home';
import AddBoxIcon from '@material-ui/icons/AddBox';
// import AccountTreeIcon from '@material-ui/icons/AccountTree';
import BusinessCenterIcon from '@material-ui/icons/BusinessCenter';

const useStyles = makeStyles(theme => ({
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
  listItem: {
    borderBottom: "1px solid " + theme.palette.primary.main
  },
  icoInList: {
    minWidth: 40
  },
  ico: {
    fontSize: 30
  }
}))

const navs = [
    {
        name: "Home",
        url: "/home",
        icon: HomeIcon
    },
    {
        name: "Create Tale",
        url: "/create-tale",
        icon: AddBoxIcon
    },
    // {
    //     name: "Tales Flow",
    //     url: "/tales-flow",
    //     icon: AccountTreeIcon
    // },
    {
        name: "My Tales",
        url: "/my-tales",
        icon: BusinessCenterIcon
    }
]

const Sidebard = props => {
  const classes = useStyles()

  const sideList = side => (
    <div
      className={classes.list}
      role="presentation"
      onClick={props.closeSidebar}
      onKeyDown={props.closeSidebar}
    >
      <List>
        {navs.map((nav, index) => (
          <Link key={nav.name} to={nav.url} className="anchor-default-none">
            <ListItem button className={classes.listItem}>
              <ListItemIcon className={classes.icoInList}>
                  <nav.icon color="primary" className={classes.ico} />
              </ListItemIcon>
              <ListItemText primary={nav.name} />
            </ListItem>
          </Link>
        ))}
      </List>
      {/* <Divider />
      <List>
        {['All mail', 'Trash', 'Spam'].map((text, index) => (
          <ListItem button key={text}>
            <ListItemIcon>{index % 2 === 0 ? <InboxIcon /> : <MailIcon />}</ListItemIcon>
            <ListItemText primary={text} />
          </ListItem>
        ))}
      </List> */}
    </div>
  )

  return (
    <div>
      <Drawer open={props.isSidebarOpen} onClose={props.closeSidebar}>
        {sideList('left')}
      </Drawer>
    </div>
  )
}

const mapStateToProps = state => ({
    isSidebarOpen: state.ui.isSidebarOpen
})

const mapDispatchToProps = dispatch => ({
    closeSidebar: () => dispatch({type: UI_SIDEBAR_CLOSE})
})

export default connect(mapStateToProps, mapDispatchToProps)(Sidebard)
