import { Home, CreateTale, TalesFlow, MyTales } from '../components/pages'

const routes = [
    {
        path: "/home",
        component: Home,
        exact: true
    },
    {
        path: "/create-tale",
        component: CreateTale,
        exact: true
    },
    {
        path: "/edit-tale/:id",
        component: CreateTale,
        exact: true
    },
    {
        path: "/tales-flow",
        component: TalesFlow,
        exact: true
    },
    {
        path: "/my-tales",
        component: MyTales,
        exact: true
    }
]

export default routes