import React, { useState } from 'react'
// import Container from '@material-ui/core/Container'
import Box from '@material-ui/core/Box'
// import Grid from '@material-ui/core/Grid'
// import Paper from '@material-ui/core/Paper'
// import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import { connect } from 'react-redux'
import ExportTaleModal from './ExportTaleModal'

function download (filename, text) {
    var element = document.createElement('a');
    element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(text));
    element.setAttribute('download', filename);

    element.style.display = 'none';
    document.body.appendChild(element);

    element.click();

    document.body.removeChild(element);
}

const CreateTaleFinish = props => {
    const { wipTale } = props
    const [isExpTaleModalVisible, setExpTModalVis] = useState(false)
    const [expTale, setExpTale] = useState('')
    const getWIPTale = () => {
        const { storyboard, ...tale } = wipTale
        return tale
    }

    const exportWIPTale = () => {
        const tale = getWIPTale()
        console.log(tale)
        const content = btoa(encodeURIComponent((JSON.stringify(tale))))
        download(`${tale.info.name}_${tale.id}.txt`, content)
        setExpTale(content)
        setExpTModalVis(true)
    }

    const saveTaleLocal = () => {
        const tale = getWIPTale()
        const allReactales = JSON.parse(window.localStorage.getItem("allReactales")) || {}
        allReactales[tale.id] = tale
        window.localStorage.setItem("allReactales", JSON.stringify(allReactales))
        alert("Saved succesfully. " + localStorageSpace())
    }

    const localStorageSpace = () => {
        var allStrings = '';
        for (var key in window.localStorage) {
            if (window.localStorage.hasOwnProperty(key)) {
                allStrings += window.localStorage[key];
            }
        }
        return allStrings ? 3 + ((allStrings.length * 16) / (8 * 1024)) + ' KB' : 'Empty (0 KB)';
    };

    return (
        <Box py={1}>
            <ExportTaleModal 
                isOpen = {isExpTaleModalVisible}
                handleClose = {() => setExpTModalVis(false)}
                expTaleContent = {expTale}
            />
            <Button
                variant="contained"
                color="primary"
                className="margin-right-15 margin-top-15"
                onClick={exportWIPTale}
            >
                Export this Reactale
            </Button>
            <Button
                variant="contained"
                color="primary"
                className="margin-top-15"
                onClick={saveTaleLocal}
            >
                Save in local
            </Button>
        </Box>
    )
}

const mapStateToProps = state => ({
    wipTale: state.wipTale
})

export default connect(mapStateToProps)(CreateTaleFinish)