import React, { useState, useEffect } from 'react'
// import { makeStyles } from '@material-ui/core/styles'
// import Paper from '@material-ui/core/Paper'
import Box from '@material-ui/core/Box'
import Grid from '@material-ui/core/Grid'
import ComplexCard from '../../common/cards/ComplexCard'

// const useStyles = makeStyles(theme => ({
//     cardsContainer: {

//     }
// }))

const MyLocalTales = props => {
    // const classes = useStyles()
    const [localTales, setLocalTales] = useState([])

    const localLocalTales = () => {
        const allReactales = JSON.parse(window.localStorage.getItem("allReactales")) || {}
        setLocalTales(Object.values(allReactales))
    }
    
    useEffect(() => {
        localLocalTales()
    }, [])

    const goToEditTale = id => {
        props.history.push(`/edit-tale/local-${id}`)
    }

    return (
        <Box py={3}>
            <Grid container spacing={2}>
                {
                    localTales.map (t => (
                        <Grid item xs={12} sm={6} md={4} lg={3} xl={2} key={t.id}>
                            <ComplexCard 
                                tale={t} 
                                expandable={false} 
                                handleEdit={goToEditTale} />
                        </Grid>
                    ))
                }
            </Grid>
        </Box>
    )
}

export default MyLocalTales