import React, { useState, useEffect, useCallback } from 'react'
// import { makeStyles } from '@material-ui/core/styles'
import Paper from '@material-ui/core/Paper'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import Box from '@material-ui/core/Box'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'
import AccountTreeIcon from '@material-ui/icons/AccountTree'
import ImportTaleModal from './ImportTaleModal'
import CreateTaleIntro from './CreateTaleIntro'
import CreateTaleBody from './CreateTaleBody'
import CreateTaleFinish from './CreateTaleFinish'
import { startsWith } from '../../../utils/util'
import { connect } from 'react-redux'
import {
    actionCreateNewTale,
    actionSaveTaleAndReset,
    actionSetTaleAsWIP
} from '../../../redux/actionCreators/createTaleActions'

const CreateTale = props => {
    const [visibleTabIndex, setVisibleTab] = useState(0)
    const [pageTitle, setPageTitle] = useState("Create")
    const [isImportModalOpen, setIsImportModalOpen] = useState(false)
    const { id, createNewTale, saveAndResetAll, match, setTaleAsWIP } = props
    
    const handleTabSwitch = (event, newValue) => {
        setVisibleTab(newValue)
    }

    const goTo = url => props.history.push(url)

    const setReactaleForEditing = useCallback(() => {
        let storyID = match.params.id
        console.log(storyID)

        // If it is a local story
        // Then the ID should start with "local"
        if (storyID.indexOf("local") >= 0) {
            storyID = storyID.trim().replace("local-", "")
            const allReactales = JSON.parse(window.localStorage.getItem("allReactales")) || {}
            const theTale = allReactales[storyID]
            setTaleAsWIP(theTale)
        }
    }, [match, setTaleAsWIP])

    const setImportedTaleForEditing = tale => {
        setTaleAsWIP(tale)
        setIsImportModalOpen(false)
    }

    useEffect(() => {
        // if already an ID is present,
        // then already a story is in progress of create / edit
        if (match.url === '/create-tale' && !id) {
            createNewTale()
        }
        // edting current in progress tale
        // coming back from FLowTale
        else if(startsWith(match.url, '/edit-tale/current')) {
            // Do nothing
            console.log('back from TalesFlow')
        }
        // else Edit Reactale
        else if (startsWith(match.url, '/edit-tale/')) {
            setPageTitle("Edit")
            setReactaleForEditing()
        }
        // Already something in progress
        // Do nothing
        else {
            console.log('Forbidden case inside create-edit... else case...')
        }

        return (() => {
            console.log("Leaving Story Create Page... Now is a good time to save changes so far...")
            //saveAndResetAll()
        })
    }, [id, createNewTale, saveAndResetAll, match, setReactaleForEditing])

    return (
        <Box p={2}>
            
            <Typography variant="h4" component="h1">
                {pageTitle} Reactale
            </Typography>
            
            <Box mt={2} mb={4} className="flex-space-between">
                <Typography variant="subtitle1">
                    {pageTitle} a Reactale or import one to edit it.
                </Typography>
                
                <Box>
                    <Button 
                        variant="contained" 
                        color="primary"
                        className="margin-right-15 margin-top-15"
                        onClick={() => setIsImportModalOpen(true)}
                    >
                        Import Reactale
                    </Button>
                    <Button 
                        variant="contained" 
                        color="primary"
                        className="margin-top-15"
                        startIcon={<span className="talesflowicon"><AccountTreeIcon /></span>}
                        onClick={() => goTo('/tales-flow')}
                    >
                        TalesFlow
                    </Button>
                </Box>
                
            </Box>

            <Paper square>
                <Tabs
                    value={visibleTabIndex}
                    indicatorColor="primary"
                    textColor="primary"
                    onChange={handleTabSwitch}
                    centered
                    aria-label="tabs to create the story"
                >
                    <Tab label="Intro" />
                    <Tab label="Body" />
                    <Tab label="Finish" />
                </Tabs>
            </Paper>
            
            <Box className={visibleTabIndex !== 0 ? 'hide' : ''}>
                <CreateTaleIntro />
            </Box>

            <Box className={visibleTabIndex !== 1 ? 'hide' : ''}>
                <CreateTaleBody />
            </Box>

            <Box className={visibleTabIndex !== 2 ? 'hide' : ''}>
                <CreateTaleFinish />
            </Box>

            {/* Import Tale Modal */}
            <ImportTaleModal 
                isOpen={isImportModalOpen} 
                handleClose = {() => setIsImportModalOpen(false)}
                setForEditing = {setImportedTaleForEditing}
            />
        </Box>
    )
}
const mapStateToProps = state => ({
    id: state.wipTale.id
})
const mapDispatchToProps = dispatch => ({
    createNewTale: () => dispatch(actionCreateNewTale()),
    saveAndResetAll: () => dispatch(actionSaveTaleAndReset()),
    setTaleAsWIP: (tale) => dispatch(actionSetTaleAsWIP(tale))
})
export default connect(mapStateToProps, mapDispatchToProps)(CreateTale)